@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&display=swap);
*{
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #dde8fc;
}

/* Hide scrol bar for Chrome, safar, opera */
body ::-webkit-scrollbar{
  display: none;
}

body{
  -ms-overflow-style: none;
  scrollbar-width: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.contaier{
  margin: 0 auto;
  max-width: 1280px;
  padding: 0 15px;
}

p{
  line-height: 30px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

h1, h2{
  font-family: 'Righteous ',cursive;
}

.home{
    flex-grow: 1;
    min-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.home .title{
    width: 100%;
    font-size: 18px;
    text-align: center;
}
.typed-text{
    font-size: 2rem;
    color:rgb(29, 3, 29)
}
.home .title p{
    line-height: 50px;
}

.home .title button{
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 24px;
    margin-top: 30px;
    color: #ffffff;
    padding: 10px 15px;
    border-radius: 999px;
    background-color: #3f5dc2;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.home .person{
    width: 100%;
    margin-top: 20px;
    text-align: center;
}

.home .person img{
    width: 70%;
    
}

.tsparticles-canvas-el{
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 1;
  }

@media (min-width: 800px){

    .home{
        flex-direction: row;
        justify-content: space-between;

    }

    .home .title,
    .home .person{
        width: 50%;
        margin-top: 0;
        text-align: left;
        font-size: 25px;
    }

    .home .person img{
        width: 100%;
    }

}
.about{
    flex-grow: 1;
}

.about h1{
    margin-bottom: 20px;
}
footer{
    width: 100%;
    padding: 10px 0;
}

footer img{
    max-width: 60px;
}

footer img:not(:last-child){
    margin-right: 10px;
}

a, a:active, a:hover {
    outline: 0;
}

.button-container {
    display: inline-block;
    height: 6rem;
    width: 6rem;
    margin: 0 1.75rem;
}

.button {
    transition: color 0.5s linear;
    height: 6rem;
    width: 6rem;
    color: var(--primaryTextColor);
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    text-decoration: none;
    position: relative;
    z-index: 1;
    border-radius: 25%;
}

.icon {
    height: 4.5rem;
    width: 4.5rem;
    padding: 1rem;
}

.icon_title {
    font-size: 1.5rem;
}

.button:hover {
    background-color: var(--shadowColor);
    box-shadow: 0 0 0.75rem 0.75rem rgba(128, 128, 128, 0.2);
}

.button:active {
    transform: scale(0.9);
}

.button-container .icon_title {
    display: none;
}

.button-container:hover .icon_title {
    display: inline;
    display: initial;
}

.button-container:hover .icon {
    display: none;
}

/* Mobile Devices */

@media only screen and (max-device-width: 820px) and (-webkit-min-device-pixel-ratio: 2) {
    .button-container {
        height: 5rem;
        width: 5rem;
        margin: 0 0.8rem;
    }

    .button {
        height: 5rem;
        width: 5rem;
    }

    .icon {
        height: 4rem;
        width: 4rem;
        padding: 0.5rem;
    }

    .icon_title {
        font-size: 1.3rem;
    }
}
.navbar{
    height: 100%;
    z-index: 2;
}

.nav-wraper{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo{
    display: flex;
    max-width: 130px;
    height: 100%;
}

.logo img{
    width: 100%;
    margin-left: -18px;
}

.list-wraper{
    display: flex;
    align-items: center;
}

.list-wraper img{
    opacity: 1;
    z-index: 4;
    width: 40px;
    right: 15px;
    padding: 8px;
    position: absolute;
    border-radius: 999px;
    background-color: #dddddd;
    transition: all 0.5s ease-in-out;
}

.list-wraper ul{
    top: 0;
    left: -100vw;
    z-index: 3;
    width: 100%;
    height: 86vh;
    padding-top: 140px;
    text-align: center;
    position: absolute;
    transition: all 0.05s ease-in-out;
    background-color: rgb(0, 0, 0, 0.8);
}

.list-wraper ul li{
    list-style: none;
    margin-bottom: 15px;
}
 h2{
     text-align: center;
     color: rgb(240, 240, 240);
     background-color: crimson;
     font-family: Georgia, 'Times New Roman', Times, serif;
 }

.list-wraper ul li a{
    display: block;
    font-size: 24px;
    padding: 10px 0;
    color: #dddddd;
    text-decoration: none;
    transition: all 0.3s ease-in-out;
}

.list-wraper ul li a:hover{
    color: #000000;
    background-color: #dddddd
}

@media (min-width:800px){

    .list-wraper img{
        display: none;
    }

    .list-wraper ul{
        padding: 0;
        width: 100%;
        height: 100%;
        text-align: left;
        position: relative;
        background-color: transparent;
    }

    .list-wraper ul li{
        display: inline-block;
        margin-bottom: 0;
    }

    .list-wraper ul li:not(:last-child){
        margin-right: 10px;
    }

    .list-wraper ul li a{
        padding: 5px;
        color:#000000;
    }

    .list-wraper ul li a:hover{
        padding: 5px;
        color: #333333;
        transform: scale(0.8);
        background: transparent;
    }




}
.skills{
    flex-grow: 1;
}

.skills h1{
    margin-bottom: 20px;
}

.content{
    display: grid;
    font-size: 24px;
    grid-template-columns: 1fr 1fr;
}

.content div{
    margin-bottom: 25px;
}

.content strong{
    line-height: 25px;
    padding-bottom: 15px;
    display: block;
}

.content li{
    margin: 0;
    margin-bottom: 10px;
}

@media(min-width: 800px){
    .content{
        grid-template-columns: 1fr 1fr 1fr;
    }
}
.app {
  display: flex;
  margin: 0 auto;
  max-width: 1280px;
  padding: 0 15px;
  min-height: 100vh;
  border-radius: 10px;
  flex-direction: column;
  background-color: #ffffff;

}

@media(min-width: 800px){
  .app{
    padding: 0 100px;
    margin-top: 5vh;
    min-height: 90vh;
  }
}

